<template>
  <div class="gameHeader">
    <div class="gameHeader_content">
      <span class="gameHeader_icon icon icon-dices"></span>
      <span class="gameHeader_title">Kingdom Dice</span>
      <span class="gameHeader_language icon icon-language pc-hide">
        <select
          class="gameHeader_language_select"
          @input="changeLang($event.srcElement.value)"
        >
          <option
            v-for="language in i18n.languages"
            :key="language.lang"
            :selected="language.lang === lang"
            :value="language.lang"
          >
            {{ language.language }}
          </option>
        </select>
      </span>
      <v-select
        :items="languages"
        item-text="text"
        item-value="val"
        :value="lang"
        class="gameHeader_language_v-select sp-hide"
        outlined
        @input="changeLang($event)"
      ></v-select>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import CommonService from '@/services/common.service'
import * as i18n from '@/globals/i18n'
import { languages } from '@/globals/globals'

export default {
  name: 'GameHeader',
  computed: mapState({
    lang: (state) => state.i18n.lang
  }),
  data: () => ({
    commonService: new CommonService(),
    i18n,
    languages
  }),
  methods: {
    changeLang (e) {
      this.commonService.changeLang(e)
    }
  }
}
</script>

<style lang="scss">
.gameHeader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: var(--primary-color);
  color: #fff;
  z-index: 1;
  &_content {
    display: flex;
    max-width: 1024px;
    padding: 0 24px;
    margin: 0 auto;
    align-items: center;
    height: 60px;
    justify-content: space-between;
  }
  &_icon {
    font-size: 32px;
  }
  &_language {
    font-size: 24px;
    line-height: 32px;
    width: 32px;
    text-align: center;
    position: relative;
    &_v-select {
      width: 140px;
      flex-grow: 0;
      .v-input__control {
        fieldset{
          color: #fff !important;
        }
      }
      .v-input__slot{
        min-height: auto !important;
      }
      .v-select__selection,
      .v-icon {
        color: #fff !important;
      }
      .v-input__slot {
        margin-bottom: 0;
      }
      .v-text-field__details {
        display: none;
      }
    }
  }
  &_title {
    font-size: 32px;
    font-family: "Cinzel", serif;
    font-weight: 700;
  }
}
@media only screen and (max-width: 359px) {
  .gameHeader {
    &_icon {
      font-size: 28px;
    }
    &_content {
      padding: 0 20px;
    }
    &_title {
      font-size: 28px;
    }
    &_fill {
      width: 5px;
    }
  }
}
@media (hover: none) and (pointer: coarse) {
  .gameHeader {
    &_language {
      &_select {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: 0;
      }
    }
  }
}
</style>
