import router from '@/router'
import store from '@/store'
import Fire from '@/plugins/firebase.js'
import AttackService from './attack.service'
import { timeouts } from '../globals/globals'

const attackService = new AttackService()
let movesRef = null
let actions = []
export default class MultiplayerService {
  initMultiplayer () {
    store.dispatch('game/changeStatus', 1)
    store.dispatch('settings/changeShowSettings', false)
    store.dispatch('game/changeResults', {})
  }

  setupMultiplayer () {
    const gameId = store.getters['game/getGameId']
    let players = store.getters['game/getPlayers']
    players = this.cleanUp(players)
    const moves = { 0: { action: 'ready' } }
    const newGameKey = process.env.NODE_ENV === 'development'
      ? gameId || 'game1'
      : gameId || Fire.database().ref().push().key
    Fire.database().ref(newGameKey).set({
      players,
      moves
    })
    store.dispatch('game/changeGameId', newGameKey)
  }

  joinMultiplayer (players) {
    const color = store.getters['game/getColor']
    const gameId = store.getters['game/getGameId']
    const playerName = store.getters['game/getPlayerName']
    store.dispatch('game/changeResults', {})
    let fullFlag = true
    let startedFlag = false
    let colorTaken = false
    let oldColor = 0
    players = players.map((player) => {
      if (player.aggresive >= 0) {
        startedFlag = true
        return player
      }
      if (!startedFlag && fullFlag) {
        if (!player.isHuman) {
          player.isHuman = true
          player.name = playerName
          store.dispatch('game/changePlayerId', player.id)
          if (colorTaken) {
            store.dispatch('game/changeColor', player.color)
          } else {
            oldColor = JSON.parse(JSON.stringify(player.color))
            player.color = color
          }
          fullFlag = false
        } else if (player.color === color) {
          colorTaken = true
        }
      } else if (player.color === color) {
        player.color = oldColor
      }
      return player
    })
    if (startedFlag) {
      return 'started'
    }
    if (fullFlag) {
      return 'full'
    }
    players = this.cleanUp(players)
    store.dispatch('game/changePlayers', players)
    Fire.database().ref(gameId).set({
      players
    })
    this.watchMoves()
    return 'ready'
  }

  changeMultiplayerName (players) {
    const gameId = store.getters['game/getGameId']
    const playerId = store.getters['game/getPlayerId']
    const playerName = store.getters['game/getPlayerName']
    players[players.findIndex(x => x.id === playerId)].name = playerName
    Fire.database().ref(gameId).set({
      players
    })
  }

  startMultiplayer () {
    const players = store.getters['game/getPlayers']
    const gameId = store.getters['game/getGameId']
    store.dispatch('game/changeStatus', 2)
    Fire.database().ref(gameId + '/players').set({
      ...players
    })
    actions = []
    this.watchMoves()
    router.push({ name: 'Game' })
  }

  joinStartMultiplayer () {
    const players = store.getters['game/getPlayers']
    const playerId = store.getters['game/getPlayerId']
    const board = store.getters['game/getBoard']
    const settings = {
      players: players.length,
      size: Math.sqrt(board.length),
      mode: 4
    }
    store.dispatch('game/changeSettings', settings)
    store.dispatch('game/changePlayer', players[0])
    store.dispatch('game/changeSelected', null)
    store.dispatch('game/changeResults', {})
    store.dispatch('game/changeStatus', 2)
    if (players[0].id === playerId) {
      store.dispatch('game/changeBusy', false)
    } else {
      store.dispatch('game/changeBusy', true)
    }
    this.watchMoves()
    router.push({ name: 'Game' })
  }

  watchMoves () {
    const gameId = store.getters['game/getGameId']
    movesRef = Fire.database().ref(gameId + '/moves')
    const self = this
    if (movesRef) movesRef.off('child_added')
    movesRef.on('child_added', function (data) {
      if (data.val().action && data.val().action === 'new') {
        store.dispatch('game/changeStatus', 6)
      }
      if (data.val().action && data.val().action === 'ready') {
        actions = []
        router.push({ name: 'Multiplayer', params: { id: gameId } })
      }
      self.listAction(data.val())
    })
  }

  listAction (data) {
    if (data) {
      actions.push({ ...data, status: 'pending' })
    }
    if (!actions.filter(action => action.status === 'executing').length) {
      const pendingI = actions.findIndex(action => action.status === 'pending')
      if (pendingI > -1) {
        actions[pendingI].status = 'executing'
        this.executeMove(actions[pendingI])
      }
    }
  }

  executeMove (move) {
    move.status = 'done'
    const playerId = store.getters['game/getPlayerId']
    if (move.from !== playerId) {
      if (move.action === 'board') {
        store.dispatch('game/changeBoard', move.board)
        attackService.processBoard()
        this.listAction()
      } else if (move.action === 'next') {
        move.status = 'executing'
        attackService.applyAdd(move)
        setTimeout(() => {
          move.status = 'done'
          attackService.moveToNextPlayer()
          this.listAction()
        }, timeouts.moveToNextPlayer)
      } else if (move.action === 'attack') {
        move.status = 'executing'
        this.executeAttack(move)
      }
    } else {
      this.listAction()
    }
  }

  executeAttack (move) {
    const board = store.getters['game/getBoard']
    const attackerId = move.attackerCell
    const i = move.defenderCell
    const attackerDice = move.attackerDice
    const defenderDice = move.defenderDice
    const defenderOwner = move.defender
    const attacker = board[attackerId]
    const defender = board[i]
    store.dispatch('game/changeResults', move)
    store.dispatch('game/changeSelected', attackerId)
    let direction = 'top'
    if (i === attackerId + 1) {
      direction = 'right'
    } else if (i === attackerId - 1) {
      direction = 'left'
    } else if (i > attackerId) {
      direction = 'bottom'
    }
    board[i].direction = direction
    board[i].defender = true
    store.dispatch('game/changeBoard', board)
    setTimeout(() => {
      defender.defender = false
      if (attackerDice.total > defenderDice.total) {
        defender.dice = attacker.dice - 1
        defender.owner = attacker.owner
        if (!attackService.checkLost(defenderOwner)) {
          store.dispatch('game/changeSelected', null)
          attackService.calculateAdd(defenderOwner)
        }
      } else {
        store.dispatch('game/changeSelected', null)
      }
      attackService.calculateAdd(attacker.owner)
      attacker.dice = 1
      store.dispatch('game/changeCell', { i, data: defender })
      store.dispatch('game/changeCell', { i: attackerId, data: attacker })
      move.status = 'done'
      this.listAction()
    }, timeouts.executeAttack)
  }

  endMultiplayer () {
    store.dispatch('game/changeBoard', [])
    store.dispatch('game/changePlayers', [])
    store.dispatch('game/changePlayer', {})
    store.dispatch('game/changeSelected', null)
    store.dispatch('game/changeResults', {})
    store.dispatch('game/changeStatus', 0)
    store.dispatch('game/changeGameId', null)
    router.push({ name: 'Game' })
    if (movesRef) movesRef.off('child_added')
  }

  again () {
    const gameId = store.getters['game/getGameId']
    const newMoveKey = Fire.database().ref(gameId).child('moves').push().key
    const move = { action: 'new' }
    if (movesRef) movesRef.off('child_added')
    Fire.database().ref(gameId + '/moves/' + newMoveKey).set({
      ...move
    }).then(snap => {
      router.push({ name: 'Multiplayer' })
    })
  }

  cleanUp (val) {
    return JSON.parse(JSON.stringify(val))
  }
}
