<template>
  <div class="multiplayer">
    <v-card>
      <v-card-title>{{isHost ? i18n.host : i18n.guest}} {{i18n.multiplayer}}</v-card-title>
      <v-card-text>
        <div v-if="isHost" class="multiplayer_links">
          <div class="multiplayer_link">
            <v-btn v-if="webShareApiSupported" depressed color="primary" large @click="share">
              <span class="icon icon-share"></span>
              {{i18n.invite}}
            </v-btn>
            <template v-else>
              <v-btn depressed color="primary" large @click="copy">
                <span class="icon icon-copy"></span>
                {{i18n.copy}}
              </v-btn>
              <input
                type="text"
                :value="'https://yannde.github.io/dicegame/?id=' + gameId"
                ref="copy"
                class="multiplayer_copy"
              />
            </template>
          </div>
          <qrcode-vue
            :value="'https://yannde.github.io/dicegame/?id=' + gameId"
            class="multiplayer_qr"
          ></qrcode-vue>
        </div>
        <div class="multiplayer_self">
          <v-text-field
            :value="game.playerName"
            @input="updatePlayerName($event)"
            :label="i18n.name"
            :placeholder="i18n.nameText"
            outlined
            class="multiplayer_name"
          ></v-text-field>
        </div>
        <div class="multiplayer_players">
          <div
            class="multiplayer_loader"
            :class="{'multiplayer_loader-loaded' : players && players.length > 1}"
          >
            <div
              v-for="player in 8"
              class="multiplayer_player multiplayer_player-loading"
              :key="player"
            ></div>
          </div>
          <div
            v-for="player in players"
            class="multiplayer_player"
            :class="[
              {'icon' : !player.name},
              {'icon-player' : player.isHuman && !player.name},
              {'icon-ai' : !player.isHuman},
              {'multiplayer_player-shortName' : nameFilter(player.name)},
              {'multiplayer_player-isPlayer' : player.id === game.playerId},
              'multiplayer_player-color-' + player.color
            ]"
            :key="player.id"
          >{{player.name | name}}</div>
        </div>
        status: {{status}}
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          v-if="isHost"
          depressed
          color="primary"
          large
          :disabled="disabled()"
          @click="startMultiplayer"
        >{{disabled() ? i18n.waiting : i18n.start}}</v-btn>
      </v-card-actions>
    </v-card>
    <v-snackbar v-model="snackbar" :timeout="2000">{{ snackbarText }}</v-snackbar>
  </div>
</template>

<script>
import Vue from 'vue'
import Fire from '@/plugins/firebase.js'
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import MultiplayerService from '@/services/multiplayer.service'
import QrcodeVue from 'qrcode.vue'

export default {
  name: 'Multiplayer',
  components: {
    QrcodeVue
  },
  computed: mapState({
    game: state => state.game,
    gameId: state => state.game.gameId,
    i18n: state => state.i18n,
    webShareApiSupported () {
      return navigator.share
    }
  }),
  data: () => ({
    gameService: new GameService(),
    multiplayerService: new MultiplayerService(),
    playersRef: null,
    gameRef: null,
    isHost: null,
    players: [],
    snackbar: false,
    snackbarText: null,
    status: 'init'
  }),
  created () {
    this.isHost = !this.$route.params.id
  },
  mounted () {
    this.multiplayerService.initMultiplayer()
    const again = !!this.gameId
    this.changePlayerId(0)
    if (this.isHost) {
      this.gameService.setPlayers()
      this.multiplayerService.setupMultiplayer()
      if (again) {
        this.initConnections()
      }
    } else {
      if (again) {
        this.initConnections()
      } else {
        this.changeGameId(this.$route.params.id)
      }
    }
  },
  methods: {
    copy () {
      this.$refs.copy.select()
      this.$refs.copy.setSelectionRange(0, 99999)
      document.execCommand('copy')
      this.snackbarText = this.i18n.copied
      this.snackbar = true
    },
    disabled () {
      return !this.players || !this.players[1] || !this.players[1].isHuman
    },
    nameFilter (val) {
      if (val && Vue.filter('name')(val).length < 3) return true
      return false
    },
    initConnections () {
      this.gameRef = Fire.database().ref(this.gameId)
      this.playersRef = Fire.database().ref(this.gameId + '/players')
      const self = this
      this.playersRef.on(
        'value',
        function (snapshot) {
          self.players = snapshot.val()
          if (!self.isHost && self.game.playerId === 0) {
            self.status = self.multiplayerService.joinMultiplayer(self.players)
            if (self.status !== 'ready') {
              self.status = status
              self.playersRef.off()
              self.gameRef.off()
            }
          }
        },
        function (error) {
          console.log('Error: ' + error.code)
        }
      )
      if (!this.isHost) {
        this.gameRef.on(
          'value',
          function (snapshot) {
            if (
              snapshot.val() &&
              snapshot.val().moves &&
              snapshot.val().players[0].aggresive >= 0 &&
              self.status === 'ready'
            ) {
              const board = snapshot.val().moves[Object.keys(snapshot.val().moves)[0]].board
              self.changeBoard(board)
              self.changePlayers(snapshot.val().players)
              self.multiplayerService.joinStartMultiplayer()
              self.gameService.finalizePlayers()
              self.playersRef.off()
              self.gameRef.off()
            }
          },
          function (error) {
            console.log('Error: ' + error.code)
          }
        )
      }
    },
    share () {
      const shareData = {
        title: 'KingdomDice',
        text: this.i18n.share,
        url: 'https://yannde.github.io/dicegame/?id=' + this.gameId
      }
      navigator.share(shareData)
    },
    updatePlayerName (val) {
      this.changePlayerName(val)
      setTimeout(() => {
        if (val === this.game.playerName) {
          this.multiplayerService.changeMultiplayerName(this.players)
        }
      }, 500)
    },
    startMultiplayer () {
      this.playersRef.off()
      this.changePlayers(this.players)
      // simplify players array
      this.gameService.setupPlayers()
      this.multiplayerService.startMultiplayer()
      this.gameService.setUpGame()
    },
    ...mapActions('game', ['changePlayers']),
    ...mapActions('game', ['changeBoard']),
    ...mapActions('game', ['changeGameId']),
    ...mapActions('game', ['changePlayerId']),
    ...mapActions('game', ['changePlayerName'])
  },
  watch: {
    gameId (val) {
      this.initConnections()
    }
  }
}
</script>

<style scoped lang="scss">
.multiplayer {
  padding: 0 20px;
  position: relative;
  width: 100%;
  &_links {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin: 0 -16px 30px;
  }
  &_qr {
    display: flex;
    justify-content: center;
  }
  &_link {
    display: flex;
    justify-content: center;
    text-align: center;
    color: var(--primary-color);
    .icon {
      margin-right: 8px;
      font-size: 22px;
    }
  }
  &_copy {
    opacity: 0;
    pointer-events: none;
    position: absolute;
  }
  &_self {
    height: 86px;
  }
  &_name {
    margin-left: 58px;
  }
  &_players {
    display: flex;
    position: relative;
    min-height: 60px;
    margin: -10px -10px 0;
    position: relative;
  }
  &_loader {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    opacity: 1;
    transition: opacity 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    &-loaded {
      opacity: 0;
    }
  }
  &_player {
    width: 48px;
    height: 48px;
    margin: 10px;
    border-radius: 4px;
    position: relative;
    line-height: 48px;
    text-align: center;
    box-sizing: border-box;
    color: #fff;
    position: relative;
    flex-shrink: 0;
    text-transform: capitalize;
    font-size: 20px;
    transition: all 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    &-shortName {
      font-size: 24px;
    }
    &-isPlayer {
      border-radius: 24px;
      position: absolute;
      top: -82px;
    }
    &-color {
      @each $i, $color in $colors {
        &-#{$i} {
          background-color: var(--player-color-#{$i});
        }
      }
    }
    &-loading {
      background: rgba(0, 0, 0, 0.12);
      &:after {
        content: "";
        height: 100%;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        transform: translateX(-100%);
        z-index: 1;
        background: linear-gradient(
          90deg,
          transparent,
          hsla(0, 0%, 100%, 0.3),
          transparent
        );
        animation: loading 1.5s infinite;
      }
    }
    &.icon {
      font-size: 34px;
    }
  }
}
</style>
