export function name (val) {
  if (!val) {
    return val
  }
  const parts = val.split(' ')
  val = parts.length > 1
    ? parts[0].substr(0, 1) + parts[1].substr(0, 1).toUpperCase() +
      (parts[2] ? parts[2].substr(0, 1).toUpperCase() : '')
    : parts[0].substr(0, 3)
  return val
}
